<template>
    <div>
        <!--  -->
        <van-row>
            <van-col span="4">
                <span class="iconfont icon-zuo" v-if="false"></span>
            </van-col>
            <van-col span="16"></van-col>
            <van-col span="4">
                <router-link tag="span" to="search" class="iconfont icon-demo01"></router-link>
            </van-col>
        </van-row>
        <!--  -->
        <!--  -->
        <div class="newp clearfix">
            <!--  -->
            <div class="left" ref="left">
                 <ul>
                    <li v-for="(item,index) in leftlist" :key="index"  @click="selectItem(index,$event)"  :class="{current:index == currentIndex}">{{item}}</li>
                </ul>
            </div>
            <!--  -->
            <div class="right" ref="right">
                <div class="right-con">
                    <img src="../assets/img/fel01.jpg" @load="gotoLoad()">
                    <ul v-for="(it,i) in rightlist" :key="i" class="right-item-hook">
                        <p><i></i>{{it.title}}<b></b></p>
                        <li class="w33 fl" v-for="(item,index) in it.con" :key="index">
                            <div><img :src="require(`../assets/img/${item.imgsrc}`)"  @load="gotoLoad()"></div>
                            <span>{{item.name}}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <!--  -->
    </div>
</template>

<script>
    import BScroll from '@better-scroll/core'    
    export default {
        data(){
            return{
                // 
                leftlist:["","","","","",""],
                //
                rightlist:[
                    {
                        title:"",
                        con:[
                            {imgsrc:"felp01.png",name:"10"},
                            {imgsrc:"felp02.png",name:"K30 Pro "},
                            {imgsrc:"felp03.png",name:"K30 Pro"},
                            {imgsrc:"felp04.png",name:""},
                            {imgsrc:"felp05.png",name:"3 Pro"},
                            {imgsrc:"felp06.png",name:"10 Pro"},
                            {imgsrc:"felp07.png",name:"10"},
                            {imgsrc:"felp08.png",name:"Redmi K30 4G"},
                            {imgsrc:"felp09.png",name:"Redmi K30 5G"},
                            {imgsrc:"felp10.png",name:"MIX Alpha"},
                        ]
                    },
                    {
                        title:"",
                        con:[
                            {imgsrc:"ilt01.png",name:"Color"},
                            {imgsrc:"ilt02.png",name:""},
                            {imgsrc:"ilt03.png",name:"3"},
                            {imgsrc:"ilt04.png",name:""},
                            {imgsrc:"ilt05.png",name:"AX3600"},
                            {imgsrc:"ilt06.png",name:""},
                        ]
                    },
                    {
                        title:"",
                        con:[
                            {imgsrc:"jia01.png",name:"4 44''"},
                            {imgsrc:"jia02.png",name:"Redmi 70''"},
                            {imgsrc:"jia03.png",name:"Pro 55''"},
                            {imgsrc:"jia04.png",name:"X"},
                            {imgsrc:"jia05.png",name:""},
                            {imgsrc:"jia06.png",name:"1C"},
                            {imgsrc:"jia07.png",name:"Redmi 98''"},
                        ]
                    },
                    {
                        title:"",
                        con:[
                            {imgsrc:"com01.png",name:"RedmiBook14"},
                            {imgsrc:"com02.png",name:"RedmiBook13"},
                            {imgsrc:"com03.png",name:""},
                        ]
                    },
                     
                    {
                        title:" ",
                        con:[
                            {imgsrc:"mi01.png",name:"10"},
                            {imgsrc:"mi02.png",name:"10Pro"},
                            {imgsrc:"mi03.png",name:"10"},
                        ]
                    },
                    {
                        title:"",
                        con:[
                            {imgsrc:"cro01.png",name:""},
                            {imgsrc:"cro02.png",name:"E"},
                            {imgsrc:"cro03.png",name:""},
                            {imgsrc:"cro04.png",name:""},
                            {imgsrc:"cro05.png",name:""},
                            {imgsrc:"cro06.png",name:""},
                            {imgsrc:"cro07.png",name:""},
                            {imgsrc:"cro08.png",name:""},
                            {imgsrc:"cro09.png",name:"Redmi"},
                            {imgsrc:"cro10.png",name:""},
                            {imgsrc:"cro11.png",name:""},
                            {imgsrc:"cro12.png",name:""},
                            {imgsrc:"cro13.png",name:""},
                            {imgsrc:"cro14.png",name:""},
                            {imgsrc:"cro15.png",name:""},
                            {imgsrc:"cro16.png",name:""},
                            {imgsrc:"cro17.png",name:"2"},
                            {imgsrc:"cro18.png",name:""},
                            {imgsrc:"cro19.png",name:""},
                        ]
                    },
                   
                ],
                listHeight:[],//
                scrollY:0
            }
        },
              methods: {
                _initScroll(){
                    // 
                    this.lefts = new BScroll(this.$refs.left,{
                        click:true, //
                        probeType:3 //
                    })
                    // 
                    this.rights = new BScroll(this.$refs.right,{
                        probeType:3, //
                    })
                    // 
                    this.rights.on("scroll",this.onScrollfun)
                },onScrollfun(pos){
                    this.scrollY =  Math.abs(Math.round(pos.y));//y
                        
                },
                _getHeight(){
                    // 
                    let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                    let height = 0; //
                    this.listHeight.push(height);
                    // 
                    for(let i = 0;i < rightItems.length;i++){
                        let item = rightItems[i];
                        height += item.clientHeight; //
                        this.listHeight.push(height);
                    }
                    
                },
                selectItem(index,event){
                    // _constructed,return
                    if(!event._constructed){
                        return;
                    }else{
                        // 
                        let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                        let el = rightItems[index];//
                        // 
                        this.rights.scrollToElement(el);
                    }
                }, 
                gotoLoad(){
                    this.rights && this.rights.refresh();
                }

            },
            // vue
            mounted() {
                // DOM
                this.$nextTick(()=>{
                    this._initScroll(); //
                    this._getHeight();
                })
            },
            beforeDestroy(){
                this.rights.off("scroll",this.onScrollfun)
            },
            // 
            computed: {
                currentIndex(){
                    for(let i = 0;i <this.listHeight.length;i++){
                        let height = this.listHeight[i];
                        let height2 = this.listHeight[i+1];
                        // 
                        if((this.scrollY >= height && this.scrollY < height2)){
                            // 
                            let leftItems = this.$refs.left.getElementsByTagName("li");
                            let elLI = leftItems[i];
                            this.lefts.scrollToElement(elLI);
                            return i;
                        }
                    }
                    // 0
                    return 0;
                }
            },
                
    }
</script>

<style lang="scss" scoped>
//
.van-row{
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: #f2f2f2;
    font-size: 16px;
}

//
.newp{
    position:absolute;
    top: 50px;
    bottom: 50px;
    overflow: hidden;
    display: flex;
    .left{
        border-right: 1px solid #efefef;
        width: 76px;
        box-sizing: border-box;
        ul{
            li{
                text-align: center;
                height: 45px;
                line-height: 45px;
                width: 76px;
                &.current{
                    color: #1989fa;
                    font-size: 14px;
                }
            }
        }
    }
    .right{
        width: 80%;
        padding: 0 20px;
        box-sizing: border-box;
        text-align: center;
        flex: 1;
        .right-con{
            overflow: hidden;
            ul{
                overflow: hidden;
                p{
                    font-size: 14px;
                    font-weight: normal;
                    position: relative;
                    margin: 20px 0px 10px;
                    position: relative;
                    i,b{
                        height: 1px;
                        width: 40px;
                        background-color: #ececec;
                        position: absolute;
                        top: 10px;
                        left: 50%;
                    }
                    i{
                        transform: translateX(-200%);
                    }
                    b{
                        transform: translateX(100%);
                    }
                }
                li{
                    margin-bottom: 8px;
                    div{
                        padding: 10px;
                        height: 86.4px;
                        box-sizing: border-box;
                    }
                    img{
                        margin: 0 auto;
                        text-align: center;
                        // width: 65%;
                    }
                    span{
                        font-size: 10px;
                        color:#999;
                        display: block;
                        white-space:nowrap;
                        overflow:hidden;
                        text-overflow:ellipsis;
                    }
                }
            }
        }
    }
}
</style>